window.dom={
    //Êñ∞Â¢ûËäÇÁÇπ
    create(string){//Áî®‰∫éÂàõÂª∫Â∏¶ÊúâÂÜÖÂÆπÁöÑÂÖÉÁ¥†
        const container=document.createElement("template")//templateÂèØ‰ª•Áî®‰∫éÊèíÂÖ•ÊâÄÊúâÊ†áÁ≠æ
        container.innerHTML=string.trim()//trimÂèØ‰ª•Áî®‰∫éÂøΩÁï•Á©∫Ê†ºÂíåÊñáÊú¨ÂÖÉÁ¥†
        return container.content.firstChild;
    },
   after(node,node2){
       node.parentNode.insertBefore(node2,node.nextsibling)//Âú®ËäÇÁÇπÂêéÈù¢Ê∑ªÂä†‰∏Ä‰∏™Êñ∞ÁöÑËäÇÁÇπÔºåÂè™ËÉΩÁî®Âú®‰∏ã‰∏Ä‰∏™ËäÇÁÇπÂâçÈù¢Ê∑ªÂä†‰∏Ä‰∏™Êñ∞ÁöÑËäÇÁÇπ„ÄÇ
   },
   before(node,node2){
       node.parentNode.insertBefore(node2,node)//Âú®nodeÂâçÈù¢Êñ∞Â¢û‰∏Ä‰∏™ËäÇÁÇπ
   },
    append(parent,node){
        parent.appendChild(node)//Áî®‰∫éÊñ∞Â¢û‰∏Ä‰∏™ÂÑøÂ≠ê
    },
    wrap(node,parent){
        dom.before(node,parent)//ÂÖàÊääÊñ∞ÁöÑËäÇÁÇπÊîæÂú®ËäÇÁÇπÁöÑÂâçÈù¢ÊàñËÄÖÂêéÈù¢
        dom.append(parent,node)//ÂÜçÊäänodeÊîæÂà∞parentÁöÑÈáåÈù¢ÔºåÂÆÉÂÖàÂâçÁöÑ‰ΩçÁΩÆ‰ºöÊ∂àÂ§±
    },
    //Âà†Èô§
    remove(node){
        node.parentNode.removeChild(node)
        return node //ÂèØËÉΩËøòË¶ÅÁî®
    },
    empty(node){
        //ÁÆÄÂÜôconst childNodes=node.childNodes===const {childNodes}=node
        const array=[]
        let x=node.firstChild
        while(x){
            array.push(dom.remove(node.firstChild))
            x=node.firstChild
        }
        return array
    },
    //ÊîπÂÜô
    attr(node,name,value){//ÈáçËΩΩÔºöÊ†πÊçÆÂèÇÊï∞‰∏™Êï∞ÂÜô‰∏çÂêåÁöÑ‰ª£Á†ÅÔºåÂè´ÈáçËΩΩ
        if(arguments.length===3){
            node.setAttribute(name,value)
        }else if(arguments.length===2){
            return node.getAttribute(name)//Ë¶ÅËøîÂõû‰∏Ä‰∏™ÂÄº
        }
    },
    text(node,string){//ÈÄÇÈÖç
        if(arguments.length===2){
            if('innerText'in node){
                node.innerText=string   //ie
             }else{
                 node.textContent=string  //ÁÅ´Áãê,chrome
             }   
        }else if(arguments.length===1){
             if('innerText'in node){
                 return node.innerText
             }else{
                 return node.textContent
             }
        }
    },
    html(node,string){
        if(arguments.length===2){//arugmentÊòØÂáΩÊï∞Ë∞ÉÁî®Êó∂ÁöÑÂÆûÈôÖÂèÇÊï∞
            node.innerHTML=string
        }else if(arguments.length===1){
            return node.innerHTML
        }
    },
    style(node,name,value){
        if(arguments.length===3){
            //dom.style(div,'color','red')
            node.style[name]=value
        }else if(arguments.length===2){
            if(typeof name==='string'){
                //dom.style(div,'color')
                return node.style[name]
            }else if(name instanceof Object){
                //dom.style(div,{color:'red'})
                const object=name
                for(let key in object){
                    node.style[key]=object[key]
                }
            }
        }
    },
    class:{
        add(node,className){
            node.classList.add(className)
        },
        remove(node,className){
            node.classList.remove(className)
        },
        has(node,className){
            return node.classList.contains(className)
        }
    },



}
